
circle {
  stroke: #ff9800; 
  stroke-width: 2px;
  stroke-opacity: 1;
  z-index:1;
  position: relative;
}

.pulse {
  fill: white;
  fill-opacity: 0;
  transform-origin: 699.866px 430.14px;
  animation-duration: 1.5s;
  animation-name: pulse;
  animation-iteration-count: infinite;
  z-index:-1;
  position: relative;
}
#stmartin{
   transform-origin:120.77747px 71.148491px ;
}

#stbarth{
   transform-origin: 302.46841px  218.833704px ;
}

#baiemahault{
   transform-origin: 622.4104px 452.01638px ;
}  

#cfapag{
   transform-origin: 700.46841px 418.833704px ;
}

@keyframes pulse {
  from {
    stroke-width: 3px;
    stroke-opacity: 1;
    transform: scale(0.3);
  }
  to {
    stroke-width: 0;
    stroke-opacity: 0;
    transform: scale(2);
  }
}
